.hamburger {
	width: 72px;
	height: 60px;
	display: block;
	/*background: orange;*/
	position: relative;
	overflow: hidden;
	float: left;
}

.hamburger > div {
	width: calc(100% - 32px);
	height: 4px;
	background-color: #ffffff;
	border-radius: 5px;
	position: absolute;
	left: 16px;
	opacity: 1;
	-webkit-transition: all 1s cubic-bezier(.25,.8,.25,1);
	transition: all 1s cubic-bezier(.25,.8,.25,1);
}

.hamburger > div:nth-child(1) {
	top:16px;
	z-index: 3;
	/*background-color: #ff0000;*/
}

.hamburger > div:nth-child(2) {
	top: 28px;
	/*background-color: #00ff00;*/
}

.hamburger > div:nth-child(3) {
	top: 40px;
	/*background-color: #0000ff;*/
}

/* cross */

.hamburger.cross > div:nth-child(1) {
	top:-4px;
	opacity: 0;
}

.hamburger.cross > div:nth-child(2) {
	top:28px;
	-webkit-transform:rotate(45deg);
	        transform:rotate(45deg);
}

.hamburger.cross > div:nth-child(3) {
	top:28px;
	-webkit-transform:rotate(-45deg);
	        transform:rotate(-45deg);
}

/* back */

.hamburger.back > div:nth-child(1) {
	top:-4px;
	opacity: 0;
}

.hamburger.back > div:nth-child(2) {
	width: 20px;
	top:22px;
	left: 19px;
	-webkit-transform:rotate(-45deg);
	        transform:rotate(-45deg);
}

.hamburger.back > div:nth-child(3) {
	width: 20px;
	top:34px;
	left: 19px;
	-webkit-transform:rotate(45deg);
	        transform:rotate(45deg);
}
.lang-selector {
	width: 72px;
	height: 60px;
	float: left;
	background-repeat: no-repeat;
	background-size: 30px 30px;
	background-position: calc(100% - 16px) 50%;
}

.lang-selector > div {
	position: absolute;
	background-repeat: no-repeat;
	background-size: cover;
	right: 16px;
	top: 15px;
	width: 30px;
	height: 30px;
	-webkit-transition: all 1s cubic-bezier(.25,.8,.25,1);
	transition: all 1s cubic-bezier(.25,.8,.25,1);
	-webkit-transform: rotate(0deg);
	        transform: rotate(0deg);
}

.lang-selector > div:nth-child(1) {
	background-image: url('/en.svg');
}

.lang-selector > div:nth-child(2) {
	background-image: url('/bi.svg');
}


.lang-selector.en > div:nth-child(1) {
	-webkit-transform: rotate(0deg) scale(1);
	        transform: rotate(0deg) scale(1);
	opacity: 1;
}

.lang-selector.en > div:nth-child(2) {
	-webkit-transform: rotate(720deg) scale(0);
	        transform: rotate(720deg) scale(0);
	opacity: 0;
}


.lang-selector.bi > div:nth-child(1) {
	-webkit-transform: rotate(720deg) scale(0);
	        transform: rotate(720deg) scale(0);
	opacity: 0;
}

.lang-selector.bi > div:nth-child(2) {
	-webkit-transform: rotate(0deg) scale(1);
	        transform: rotate(0deg) scale(1);
	opacity: 1;
}

