.chapter {
	display: table;
	background-color: white;
	padding: 16px;
	width: 100%;
	border-top: 1px solid #f0f0f0;
}

.chapter .name {
	display: table-cell;
    vertical-align: middle;
	float: left;
	min-height: 22px;
	width: calc(100% - 22px);
}

.chapter .arrow {
	display: table-cell;
    vertical-align: middle;
	width: 22px;
	min-height: 22px;
	float: right;
}

.chapter .arrow:before,
.chapter .arrow:after  {
	display: block;
	content: "";
	width: 15px;
	height: 3px;
	background-color: #ddd;
	border-radius: 3px;
}

.chapter .arrow:before {
	position: absolute;
	-webkit-transform:rotate(45deg);
	        transform:rotate(45deg);
	top: 5px;
}

.chapter .arrow:after {
	position: absolute;
	-webkit-transform:rotate(-45deg);
	        transform:rotate(-45deg);
	bottom: 5px;
}

.type-toggle {
	width: 100%;
	margin-top: -16px;
	padding: 8px 0px;
	height: 53px;

}

.type-toggle > div {
	color: rgba(255,255,255,0.5);
	text-align: center;
	float: left;
	width: 33.333%;
	padding: 8px;
	letter-spacing: 3px;
	cursor: pointer;
	-webkit-transition: all 0.5s cubic-bezier(.25,.8,.25,1);
	transition: all 0.5s cubic-bezier(.25,.8,.25,1);
}

.type-toggle > div.active {
	color: #fff;
	border-color: #f5f5f5;
}

.type-toggle:before {
	position: absolute;
	width: 33.333%;
	height: 37px;
	content: "";
	color: #fff;
	border-color: #f5f5f5;
	border-radius: 50px;
	background: rgba(0,0,0,0.1);
	-webkit-transition: all 0.5s cubic-bezier(.25,.8,.25,1);
	transition: all 0.5s cubic-bezier(.25,.8,.25,1);
}

.type-toggle.type-1:before {
	left: 0%;
}

.type-toggle.type-2:before {
	left: 33.333%;
}

.type-toggle.type-3:before {
	left: 66.666%;
}

