.block {
	display: block;
}

.none {
	display: none;
}

.visible {
	-webkit-animation:toVisible 1s cubic-bezier(.25,.8,.25,1) forwards;
	        animation:toVisible 1s cubic-bezier(.25,.8,.25,1) forwards;
}

.hidden {
	-webkit-animation:toHidden 1s cubic-bezier(.25,.8,.25,1) forwards;
	        animation:toHidden 1s cubic-bezier(.25,.8,.25,1) forwards;
}


@-webkit-keyframes toVisible {
    0% {
    	display:none;
    	overflow: hidden;
    	height: 0;
        opacity: 0;
    }

    50% {
    	display:block;
    	overflow: all;
    	height: auto;
        opacity: 0;
    }

    100% {
    	display:block;
    	overflow: all;
    	height: auto;
        opacity: 1;
    }
}


@keyframes toVisible {
    0% {
    	display:none;
    	overflow: hidden;
    	height: 0;
        opacity: 0;
    }

    50% {
    	display:block;
    	overflow: all;
    	height: auto;
        opacity: 0;
    }

    100% {
    	display:block;
    	overflow: all;
    	height: auto;
        opacity: 1;
    }
}

@-webkit-keyframes toHidden {
    0% {
    	display:block;
    	overflow: all;
    	height: auto;
        opacity: 1;
    }

    99.99999% {
    	display:block;
    	overflow: all;
    	height: 0;
        opacity: 0;
    }

    100% {
    	display:none;
    	overflow: hidden;
    	height: 0;
        opacity: 0;
    }
}

@keyframes toHidden {
    0% {
    	display:block;
    	overflow: all;
    	height: auto;
        opacity: 1;
    }

    99.99999% {
    	display:block;
    	overflow: all;
    	height: 0;
        opacity: 0;
    }

    100% {
    	display:none;
    	overflow: hidden;
    	height: 0;
        opacity: 0;
    }
}

.tVisible {
    -webkit-animation:toTVisible 1s cubic-bezier(.25,.8,.25,1) forwards;
            animation:toTVisible 1s cubic-bezier(.25,.8,.25,1) forwards;
}

.tHidden {
    -webkit-animation:toTHidden 0.5s cubic-bezier(.25,.8,.25,1) forwards;
            animation:toTHidden 0.5s cubic-bezier(.25,.8,.25,1) forwards;
}

@-webkit-keyframes toTVisible {
    0% {
    	display:none;
        overflow: all;
        height: auto;
        opacity: 1;
        top: 200vh;
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        z-index: 1;
    }

    0.00001% {
    	display:block;
    }

    100% {
        top: 0px;
    }
}

@keyframes toTVisible {
    0% {
    	display:none;
        overflow: all;
        height: auto;
        opacity: 1;
        top: 200vh;
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        z-index: 1;
    }

    0.00001% {
    	display:block;
    }

    100% {
        top: 0px;
    }
}

@-webkit-keyframes toTHidden {
    0% {
    	display:block;
    	overflow: all;
    	height: auto;
        opacity: 1;
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        z-index: -1;
        top: 0;
    }

    99.99999% {
        opacity: 0;
        -webkit-transform: scale(0.5, 0.5);
                transform: scale(0.5, 0.5);
        top: 10vh;
    }

    100% {
    	display:none;
    	overflow: hidden;
    	height: 0;
        top: -99999px;
    }
}

@keyframes toTHidden {
    0% {
    	display:block;
    	overflow: all;
    	height: auto;
        opacity: 1;
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        z-index: -1;
        top: 0;
    }

    99.99999% {
        opacity: 0;
        -webkit-transform: scale(0.5, 0.5);
                transform: scale(0.5, 0.5);
        top: 10vh;
    }

    100% {
    	display:none;
    	overflow: hidden;
    	height: 0;
        top: -99999px;
    }
}


@media (max-width: 576px) {
    .cutter.diagonal {
        height: 160px;
        -webkit-clip-path: none;
                clip-path: none;
    }
    .cutter.flat {
        height: 60px;
    }
